@import "../../style-config/variable.scss";
@import "../../style-config/mixins.scss";

.skills-content-wrapper{
    display: flex;
    align-items: center;
    flex-direction: column;
    @include small-screen{
        flex-direction: row;
    }
    .left-col{
        width: 75%;
        display: flex;
        align-items: center;
        justify-content: flex-start;
        @include small-screen{
            width: 40%;
        }
        img{
            width:100%;
            max-width: 453px;
            display: block;
        }
    }
    .right-col{
        width:100% ;
        color: $white;
        
        text-align: center;
        margin-top: 25px;
        @include small-screen
        {
            margin-left: 70px;
            width: 60%;
            text-align: left;
            margin-top: 25px;

        }
        h2{
            margin-bottom: 10px;
            @include small-screen{
                margin-bottom: 30px;
            }
        }
        p{
            
            @include typography(14px ,400px,20px );
            margin-bottom: 15px;
            text-align: left;
            padding: 20px;
            @include small-screen{
                @include typography(18px ,400px,24px );
                &:hover{
                    filter: drop-shadow(1px 1px 75px $yellow);
                }
            }
            
        }
        .call-to-action{
            margin: 0 auto;
            @include small-screen{
                margin: 0;
            }
        }
    }
}